import axios from 'axios';
import qs from 'qs'
import {Message,MessageBox} from 'element-ui';
const config = Symbol('config')
const isCompleteURL = Symbol('isCompleteURL')
const requestBefore = Symbol('requestBefore')
const requestAfter = Symbol('requestAfter')

axios.interceptors.response.use(response => {
    //当返回信息为未登录或者登录失效的时候重定向为登录页面
    if (response.data&&response.data.code == 401) {
      MessageBox.confirm("权限不足，请联系管理员！", '温馨提示', {
        confirmButtonText: '确定',
        type: "warning"
      }).then(function () {
        console.error("权限不足，请联系管理员！");
      })
    }
    if (response.data&&response.data.code == 302) {
      MessageBox.confirm("会话过期，请重新登录！", '温馨提示', {
        confirmButtonText: '确定',
        type: "warning"
      }).then(function () {
        window.location.reload();
      })
    }
    return response;
  },
  error => {
    checkSession();
    return Promise.reject(error)
  }
)

function checkSession() {
  debugger
  axios.get("/api/sys/checkSession").then(function (response) {
    debugger
  }).catch(function (error) {
    console.log(error)
  });
}

class MinRequest {
  [config] = {
    header: {
      'content-type': 'application/x-www-form-urlencoded; charset=UTF-8'
    },
    dataType: 'json',
    responseType: 'text'
  }
  interceptors = {
    request: (func) => {
      if (func) {
        MinRequest[requestBefore] = func
      } else {
        MinRequest[requestBefore] = (request) => request
      }

    },
    response: (func) => {
      if (func) {
        MinRequest[requestAfter] = func
      } else {
        MinRequest[requestAfter] = (response) => response
      }
    }
  }

  static [requestBefore] (config) {
    return config
  }

  static [requestAfter] (response) {
    return response
  }

  static [isCompleteURL] (url) {
    return /(http|https):\/\/([\w.]+\/?)\S*/.test(url)
  }

  setConfig (func) {
    this[config] = func(this[config])
  }


  request (options = {}) {
    options.baseURL = options.baseURL || this[config].baseURL
    options.dataType = options.dataType || this[config].dataType
    options.url = MinRequest[isCompleteURL](options.url) ? options.url : (options.baseURL + options.url)
    options.data = qs.stringify(options.data)//数据进行转换
    options.header = {...options.header, ...this[config].header}
    options.method = options.method || this[config].method

    options = {...options, ...MinRequest[requestBefore](options)}

    return new Promise((resolve, reject) => {
      axios({
        method: options.method,
        url: options.url,
        data: options.data,
        dataType:options.dataType,
        headers: options.header,
      }).then(res => {
          resolve(res.data);
        }).catch(err => {
          reject(err);
        });
    });
  }

  get (url, data, options = {}) {
    //this[config].header.Cookie="JSESSIONID="+uni.getStorageSync("sessionId")
    options.url = url
    options.data = data
    options.method = 'GET'
    return this.request(options)
  }

  post (url, data, options = {}) {
    //this[config].header.Cookie="JSESSIONID="+uni.getStorageSync("sessionId")
    options.url = url
    options.data = data
    options.method = 'POST'
    return this.request(options)
  }
}

MinRequest.install = function (Vue) {
  Vue.mixin({
    beforeCreate: function () {
      if (this.$options.minRequest) {
        console.log("api:",this.$options.minRequest)
        Vue._minRequest = this.$options.minRequest
      }
    }
  })
  Object.defineProperty(Vue.prototype, '$request', {
    get: function () {
			return Vue._minRequest
		}
  })
}

export default MinRequest



