// 文件工具类
import $global from '../config/global'
// const HOST = $global.HOST
const FILE_BASE_PATH = $global.FILE_BASE_PATH
//文件批量上传接口
function uploadFile(url, file) {
  return new Promise((resolve, reject) => {
    uni.uploadFile({
      url: url,
      files:[{name:'file', file:file,uri:file}],
        success: function (res) {
          resolve(res)
        },
        fail: (err) => {
          reject(err)
        },
    });
  });
}

function getHisFiles(refId) {
  return new Promise((resolve, reject) => {
    console.log("[log]:", "【获取历史附件】", refId);
    $http.get("/api/applet/sysAttach/getFileHistory",{ refId: refId }).then(res => {
      for (let i = 0; i < res.list.length; i++) {
        res.list[i]["savePath"] = FILE_BASE_PATH + res.list[i]["savePath"];
      }
      resolve(res.list);
    });
  });
}

function deleteFiles(files) {
  for (let i = 0; i < files.length; i++) {
    $http.get("/api/applet/sysAttach/deleteFile",{id: files[i]["id"]}).then(res => {
      if (res.isOk) {
        console.log("[log]:", "【提交删除成功】");
      }
    });
  }
}

//清除文件缓存接口
function clearFileCache() {
  $http.get("/api/applet/sysAttach/clearFileCache", { userToken: uni.getStorageSync("userToken") }).then(res => {
  });
}

//清除文件缓存接口
function clearObj(obj) {
  for (let key in obj) {
    obj[key] = "";
  }
  return obj;
}

export default {
  uploadFile,
  getHisFiles,
  deleteFiles,
};



