import MinRequest from './MinRequest'
import $upload from './../tools/upload'
import de from "element-ui/src/locale/lang/de";

const minRequest = new MinRequest()
// 请求拦截器
// minRequest.interceptors.request((request) => {
//     return request
// })
// // 响应拦截器
// minRequest.interceptors.response((response) => {
//     return response.data
// })
// 设置默认配置:小程序端需要设置基础路径
const baseURL = "";
console.log(baseURL)
minRequest.setConfig((config) => {
    config.baseURL = baseURL;
    return config
})
export default {
    //get请求
    doGet(url, data) {
        return minRequest.get(url, data)
    },
    //post请求
    doPost(url, data) {
        return minRequest.post(url, data)
    },
    getClientToken(data) {
      return minRequest.get('/api/author/getToken', data)
    },
    //获取用户菜单
    getMenuUserTree(data) {
      return minRequest.post('/api/sys/getMenuUserTree', data)
    },
    //获取用户路由
    getUserRoutes(data) {
      return minRequest.post('/api/sys/getUserRoutes', data)
    },
    //获取数据字典
    getDictList(data) {
      return minRequest.post('/api/sys/getDictList', data)
    },
    //获取配置信息
    getConfig(data) {
      return minRequest.post('/api/sys/getConfig', data)
    },
    //退出登录
    getLoginOut(data) {
      return minRequest.post('/api/login/loginOut', data)
    },
    //获取用户数据
    getUserInfo(data) {
        return minRequest.post('/api/login/getUserInfo', data)
    },
    //获取登录信息
    getLoginInfo(data) {
        return minRequest.post('/api/login/getLoginInfo', data)
    },
    //获取文件历史
    getFileHistory(data) {
        return minRequest.get('/api/sysAttach/getFileHistory?refId='+data.refId)
    },
    //删除文件
    deleteFile(data) {
        return minRequest.get('/api/sysAttach/deleteFile?id='+data.id)
    },
    //文件上传
    uploadFiles(uploadType, attachType, file) {
        return $upload.uploadFile("/api/sysAttach/uploadFile/" + uploadType + "/" + attachType, file)
    },
    //*************************************客户端接口************************************
    //获取登录信息
    getClientLoginInfo(data) {
      return minRequest.post('/api/client/login/getLoginInfo', data)
    },
      //更新用户数据
    getClientUserInfo(data) {
      return minRequest.post('/api/login/getUserInfo', data)
    }

}



