import {Message,MessageBox} from 'element-ui';
import Vue from 'vue'

export default {
  //获取数据字典脚标
  getArrayIndex(dict, value) {
    if (value != "") {
      for (let i = 0; i < dict.length; i++) {
        if (dict[i].value == value) {
          return i
          break;
        }
      }
    }
  },
  //根据字典获取数据文本
  getArrayName(dict, value) {
    for (let i = 0; i < dict.length; i++) {
      if (dict[i].value == value) {
        return dict[i].name
        break;
      }
    }
  },
  //根据角标获取字典值
  getArrayValue(dict, index) {
    for (let i = 0; i < dict.length; i++) {
      if (i == index) {
        return dict[i].value
        break;
      }
    }
  },
};
//获取图片完整路径
Vue.prototype.formatUrl = function(url) {
  if (url != null && url != undefined && url != "") {
    if (url.indexOf("http") > -1) {
      return url
    } else {
      return this.$global.FILE_BASE_PATH + url
    }
  }
}

//清空对象值
Vue.prototype.resetForm = function(refForm) {
  if(refForm){
    refForm.resetFields();
  }
};
Vue.prototype.resetFields = function(obj) {
  for(let key in obj){
    if( obj[key] instanceof Array){
      obj[key]=[];
    }else{
      obj[key]="";
    }
  }
  return obj
};

Array.prototype.contain = function (e) {
  for (let i = 0; i < this.length; i++) {
    if (this[i] == e) {
      return true;
    }
  }
  return false;
}
// 克隆集合
Array.prototype.clone = function(){
  let a=[];
  for(let i=0,l=this.length;i<l;i++) {
    a.push(this[i]);
  }
  return a;
}

Vue.prototype.showToast = function (title) {
  Message({
    message: title,
    center: true
  });
},
Vue.prototype.showConfirm = function (content, callBack) {
  MessageBox.confirm(content, '温馨提示', {
    confirmButtonText: '确定',
    cancelButtonText: '取消',
    type: "warning"
  }).then(function () {
    if (callBack) {
      callBack()
    }
  })
},
Vue.prototype.showSuccess = function (title) {
  Message({
    message: title,
    type: 'success',
    center: true
  });
},
Vue.prototype.showWarn = function (title) {
  Message({
    message: title,
    type: 'warning',
    center: true
  });
}

Vue.prototype.focusLastIndex = function (obj) {
  this.$nextTick(() => {
    debugger
    if (window.getSelection) {
      //ie11 10 9 ff safari
      obj.focus(); //解决ff不获取焦点无法定位问题
      var range = window.getSelection(); //创建range
      range.selectAllChildren(obj); //range 选择obj下所有子内容
      range.collapseToEnd(); //光标移至最后
    } else if (document.selection) {
      var range = document.selection.createRange(); //创建选择对象
      //var range = document.body.createTextRange();
      range.moveToElementText(obj); //range定位到obj
      range.collapse(false); //光标移至最后
      range.select();
    }
  });
}











